<template>
  <!-- <div class="login">
    
    <div class="login-item">
      <label></label><input type="text" name="username">
    </div>
    <div class="login-item">
      <label></label><input type="password" name="password">
    </div>
    <div class="btn success" @click="login"></div>
    <p>{{msg}}</p>
  </div> -->

  <div class="login">
    <div class="login-screen">
      <div class="login-icon">
        <img src="../assets/less/img/login/icon.png" alt="Welcome to Mail App" />
        <h4>Welcome to <small>Day Report</small></h4>
      </div>

      <div class="login-form">
        <div class="form-group">
          <input type="text" class="form-control login-field" value="" name="username" placeholder="" id="login-name" />
          <label class="login-field-icon fui-user" for="login-name"></label>
        </div>

        <div class="form-group">
          <input type="password" class="form-control login-field" value="" name="password" placeholder="" id="login-pass" />
          <label class="login-field-icon fui-lock" for="login-pass"></label>
        </div>

        <a class="btn btn-primary btn-lg btn-block" @click="login"> </a>
        <p class="text-center" style="color: #E74C3C;"><small>{{msg}}</small></p>
        <!-- <a class="login-link" href="#">Lost your password?</a> -->
      </div>
    </div>
  </div>
</template>

<script>
function setSessionStorage(key,value){
  window.sessionStorage[key] = value;
}
export default {
  name: 'login',
  data () {
    return {
      msg: ''
    }
  },
  mounted(){

  },
  methods: {
    login(){
      var that = this;
      var username = $('[name=username]').val();
      var password = $('[name=password]').val();
      $.ajax({
        url: '/dayreport/api/login',
        data: {
          username: username,
          password: password
        },
        type: 'POST',
        dataType: 'json',
        success: function(res) {
          if(res.code != 200){
            that.msg = res.msg;
          }else{
            setSessionStorage('report_token', res.data.token);
            setSessionStorage('report_username', res.data.username);
            that.$wii.toast.show({
              text: '',
              type: 'success',
              time: 1500
            });
            setTimeout(() => {
              that.$router.push({name: 'index'});
              that.$bus.emit('loginTypeChange',true);
              that.$bus.emit('loginNameChange');
            },1000);
          }
        }
      });
    }
  }
}
</script>
<style lang="less">
.container{
  width: 970px !important;
}
</style>



